const accents = [
    [/[a]/g, 'a'],
    [/[e]/g, 'e'],
    [/[i]/g, 'i'],
    [/[o]/g, 'o'],
    [/[u]/g, 'u'],
    [/[n]/g, 'n'],
    [/ +/g, ' '],
]

const getItemRegex = text => {
    let regexText = text.toLocaleLowerCase()
    accents.forEach(accent => regexText = regexText.replace(accent[0], accent[1]))
    const regex = new RegExp(regexText, 'i')
    return regex
}
export default getItemRegex;